const axios = require("axios");

const getVehicleMakes = function () {
  return [
    "Audi",
    "Ford",
    "Hyundai",
    "Jaguar",
    "Kia",
    "Nissan",
    "Porsche",
    "Polestar",
    "Rivian",
    "Tesla",
  ];
};

const getAllVehicles = function () {
  return axios
    .get(
      // "http://localhost:8000/vehicles/all/models"
      "https://hofpnvfyu2.execute-api.us-east-1.amazonaws.com/vehicles/all/models"
    )
    .then((response) => {
      // console.log(response.data)
      console.log('getAllVehicles')
      return response.data;
    })
    .catch(function (error) {
      console.log(error);
    });
};

module.exports = {
  getVehicleMakes: getVehicleMakes,
  getAllVehicles: getAllVehicles,
};
